    right: 10px;
    cursor: pointer;
    color: #075a81;
    margin-top: 8px;
} 

.list p input {
    background-color: transparent;
    border: 0;
    color: black;
    font-size: 20px;
    background-color: white;
} 

.icons{
    margin: 0 5px; 
    font-size: 20px;
}

.list p span:hover {
    color: red;
} 

