#second{
    outline:0; 
    font-size:2vh;
    font-weight:600;
    font-family: Montserrat;
    background:#fff;
    border:none;
    padding:1.5vh 3vh;
    transition:all .3s ease-out; 
    color: #075a81;
    box-shadow:inset 0 -8px 0 0 rgba(0,0,0,.2),
      1px 1px 0 0 #097988,
      2px 2px 0 0 #097988,
      3px 3px 0 0 #097988,
      4px 4px 0 0 #097988,
      5px 5px 0 0 #097988,
      6px 6px 0 0 #097988,
      7px 7px 0 0 #097988,
      8px 8px 0 0 #097988,
      9px 9px 0 0#097988,
      10px 10px 0 0#097988,
      11px 11px 0 0 #097988,
      12px 12px 0 0 #097988;
  }

  #second:hover{
    color:#444;
  }
  #second:active{
    color:#222;
    box-shadow:inset 0 -4px 0 0 rgba(0,0,0,.2),
      1px 1px 0 0 #097988,
      2px 2px 0 0 #097988,
      3px 3px 0 0 #097988,
      4px 4px 0 0 #097988,
      5px 5px 0 0 #097988;
  }
 

  input{
    font-size: 1.9vh;
    box-sizing: content-box;
    width: 30vw;
    font-family: inherit;
    border-width:0px;
    border:none;
    padding-left: 0.8vw;
    padding-top: 0.8vw;
    padding-bottom: 0.8vw;

    box-shadow:inset 0 -8px 0 0 rgba(0,0,0,.2),
    1px 1px 0 0 #097988,
    2px 2px 0 0 #097988,
    3px 3px 0 0 #097988,
    4px 4px 0 0 #097988,
    5px 5px 0 0 #097988,
    6px 6px 0 0 #097988,
    7px 7px 0 0 #097988,
    8px 8px 0 0 #097988,
    9px 9px 0 0#097988,
    10px 10px 0 0#097988,
    11px 11px 0 0 #097988,
    12px 12px 0 0 #097988;
  }


input:focus {
    outline-width: 0;
  }
  
  input:focus::-webkit-input-placeholder {
    color: transparent;
  }
  
  input:focus:-ms-input-placeholder {
    color: transparent;
  }
  
  input:focus::placeholder {
    color: transparent;
  }
